.App{
-ms-flex-wrap: wrap;
    flex-wrap: wrap;
display: -ms-flexbox;
display: flex;
-ms-flex-pack: distribute;
    justify-content: space-around;
}
html{
	background:#748aad;
	height: 100%;
}
body,.App,#root{
    min-height: 100%;
}
.hidden {
    display: none;
}
.rounded{
    border-radius: 50%
}
.button-default {
	cursor: pointer;
	border-radius:.30rem;
    color: #fff;
    background-color: #007bff;
	border-color: #007bff;
	-webkit-transition: background-color 0.3s ease-in-out,border-color .3s ease-in-out;
	-o-transition: background-color 0.3s ease-in-out,border-color .3s ease-in-out;
	transition: background-color 0.3s ease-in-out,border-color .3s ease-in-out;
}
.button-default:hover {
	text-decoration: none;
	color: #fff;
	background-color: #0069d9;
	border-color: #0062cc;
}
.button-default:active {
	position:relative;
	top:1px;
}
.instruction-text{
	width:100%;
}


