.dragable {
    height: 40px;
    width: 60px;
    border: 1px dashed black;
    position: absolute;
    /* display: inline; */
}
.resizer {
    position: absolute;
    height: 10px;
    width:10px;
}
.top-resizer {
    top:-2px;
}
.left-resizer {
    left:-2px;
}
.bot-resizer {
    bottom:-2px;
}
.right-resizer {
    right:-2px;
}


